<template>
   <el-card shadow='hover'>
      <el-form ref="form" :model="form" :rules='rules' label-width="150px" style="width:50%;margin-top:30px">
        <el-form-item label="当前密码" prop="user_pass_old" style="margin-bottom:30px">
            <el-input  show-password placeholder="密码长度大于5位小于30位" v-model.trim="form.user_pass_old"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="user_pass" style="margin-bottom:30px">
            <el-input show-password  placeholder="密码长度大于5位小于30位"  v-model.trim="form.user_pass"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" prop="user_pass_compare" style="margin-bottom:30px">
            <el-input show-password  placeholder="密码长度大于5位小于30位"  @keyup.enter.native="onSubmit"
             v-model.trim="form.user_pass_compare"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :loading = 'loading'
             @click.native.prevent="onSubmit">确定</el-button>
        </el-form-item>
        </el-form>
  </el-card>
</template>
<script>
import { md5Encryption } from "@/utils";
export default {
    name: 'ChangePwd',
    data() {
      return {
        form: {
         user_pass: '',
         user_pass_compare: '',
         user_pass_old: ''
        },
        loading:false,
        rules: {
          user_pass_old: [
            { required: true, message: '请输入当前密码', trigger: 'blur' },
            { min: 6,max:30, message: '密码长度大于5位小于30位', trigger: 'blur' }
          ],
          user_pass: [
            { required: true, message: '请输入新密码', trigger: 'blur' },
            { min: 6,max:30, message: '密码长度大于5位小于30位', trigger: 'blur' }
          ],
          user_pass_compare: [
            { required: true, message: '请输入确认新密码', trigger: 'blur' },
            { min: 6,max:30, message: '密码长度大于5位小于30位', trigger: 'blur' }
          ],
      
        }
      }
    },
    methods: {
      onSubmit() {
        this.$refs.form.validate(valid => {
          if (valid) {
             const { user_pass, user_pass_old,user_pass_compare} = this.form;
            if(user_pass_compare === user_pass){
              this.loading = true
              const data = {user_pass:md5Encryption(user_pass),user_pass_old:md5Encryption(user_pass_old)}
              this.$store.dispatch('user/changepassword',data).then(()=>{
                this.$refs.form.resetFields()
                 this.$store.dispatch('message/hasnew')
                this.loading = false
                this.$message({
                      message: '更新密码成功！',
                      type: 'success'
                  });
              }).catch(()=>{
                 this.loading = false
              })
            }else{
              this.$message({
                      message: '确认新密码设置不一致',
                      type: 'error'
                  });
            }
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
}
</script>

